import uniCountdown from "../../components/uni-countdown.vue"; 
import uniPopup from "../../components/uni-popup.vue"
 let page =1
export default {
    onLoad(option) {
        page = 1
        this.goodsList = []
        this.loadListId = option
        this.getprolst() 
        this.getprominfo()
    },
    data(){
        return {
            goodsList:[],
            promoinfos:'',
            showPopupBottom: false,
            loadListId:{},
            activity_time:{
                hour:0,
                minute:0,
                second:10
            },  // 秒杀时间
            loadingType:0,
            valueStatus: 0,  // 更新页面状态 防止页面卡死
        }
    },
    components:{
        uniCountdown,
        uniPopup
    },
    showPopupBottom(){
        /**
         * 更新页面状态 防止页面卡死
         */
        
        this.statusMath(this)
    },
    onReachBottom:function(){
        // 上拉加载更多
        if(this.loadingType == 1 || this.loadingType == 2) { return false }
        this.loadingType = 1
        page++
		this.getprolst()
	},
    methods: {
        timeuptime(){
            page = 1
            this.goodsList = []
            this.loadListId = option
            this.getprolst()
            this.getprominfo()

        },
        hidePopup(){
            this.showPopupBottom = false
        },
        /**
         * 获取店铺活动商品列表接口
         */
        getprolst(){
            this.$http.getprolst({...this.loadListId,page:page}).then(res=>{
                if(res.status == 200) {
                    this.statusMath(this)
                    if(res.data.length == 0) {
                        this.loadingType = 2
                        return false
                    }
                    this.goodsList = [...this.goodsList,...res.data]
                    this.loadingType = 0   
                }
            })
        },
        /**
         * 
         * 获取商家促销活动信息接口
         */
        getprominfo(){
            this.$http.getprominfo(this.loadListId).then(res=>{
                if(res.status == 200) {
                    this.promoinfos = res.data
                    let str = Number(this.promoinfos.end_time) - Number(this.promoinfos.dqtime)
                    let  strArr = this.$formatSeconds(str).split('-')
                    console.log(strArr)
                    this.activity_time = {
                        day: parseInt(strArr[0] / 24),
                        hour:Number(strArr[0] % 24),
                        minute:Number(strArr[1]),
                        second:Number(strArr[2])
                    }
                }
            })
        }
    },
}
